.scrollup {
    position: fixed;
    right: 2.5rem;
    bottom: -20%;
    background-color: var(--color-white);
    color: var(--color-bg);
    opacity: .8;
    padding: 0.3rem 0.8rem;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: var(2);
    transition: 0.4s;
    border-radius: 50%;
}

.scrollup__icon {
    font-size: 1.5rem;
}

.scrollup:hover {
    /* background-color: var(--title-color-dark); */
}

.show-scroll {
    bottom: 3rem;
}

